export type RequestStatus = "pending" | "approved" | "rejected"

export interface BorrowRequest {
  id: string
  name: string
  item: string
  purpose: string
  date: string
  status: RequestStatus
  createdAt: string
}

// In-memory storage for demonstration
let requests: BorrowRequest[] = []

export const submitRequest = async (data: Omit<BorrowRequest, "id" | "status" | "createdAt">): Promise<BorrowRequest> => {
  try {
    // Simulate API delay
    await new Promise(resolve => setTimeout(resolve, 500))
    
    const newRequest: BorrowRequest = {
      id: Date.now().toString(),
      status: "pending",
      createdAt: new Date().toISOString(),
      ...data,
    }
    requests.push(newRequest)
    return newRequest
  } catch (error) {
    throw new Error("Gagal mengirim permintaan. Silakan coba lagi.")
  }
}

export const fetchRequests = async (): Promise<BorrowRequest[]> => {
  try {
    // Simulate API delay
    await new Promise(resolve => setTimeout(resolve, 300))
    return [...requests].sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime())
  } catch (error) {
    throw new Error("Gagal mengambil data permintaan.")
  }
}

export const updateRequestStatus = async (id: string, status: RequestStatus): Promise<BorrowRequest> => {
  try {
    // Simulate API delay
    await new Promise(resolve => setTimeout(resolve, 400))
    
    const index = requests.findIndex(req => req.id === id)
    if (index === -1) {
      throw new Error("Permintaan tidak ditemukan")
    }
    
    requests[index].status = status
    return requests[index]
  } catch (error) {
    throw new Error("Gagal mengupdate status permintaan.")
  }
}

export const getApprovedRequests = async (): Promise<BorrowRequest[]> => {
  try {
    const allRequests = await fetchRequests()
    return allRequests.filter(req => req.status === "approved")
  } catch (error) {
    throw new Error("Gagal mengambil data permintaan yang disetujui.")
  }
}
