"use client"

import { BorrowRequest } from "../lib/api"
import { useState } from "react"

interface RequestCardProps {
  request: BorrowRequest
  onApprove: (id: string) => void
  onReject: (id: string) => void
}

export default function RequestCard({ request, onApprove, onReject }: RequestCardProps) {
  const [isProcessing, setIsProcessing] = useState(false)

  const handleApprove = async () => {
    setIsProcessing(true)
    try {
      await onApprove(request.id)
    } finally {
      setIsProcessing(false)
    }
  }

  const handleReject = async () => {
    setIsProcessing(true)
    try {
      await onReject(request.id)
    } finally {
      setIsProcessing(false)
    }
  }

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'pending':
        return (
          <span className="inline-flex items-center rounded-full bg-yellow-50 px-2 py-1 text-xs font-medium text-yellow-800 ring-1 ring-inset ring-yellow-600/20">
            Menunggu Persetujuan
          </span>
        )
      case 'approved':
        return (
          <span className="inline-flex items-center rounded-full bg-green-50 px-2 py-1 text-xs font-medium text-green-800 ring-1 ring-inset ring-green-600/20">
            Disetujui
          </span>
        )
      case 'rejected':
        return (
          <span className="inline-flex items-center rounded-full bg-red-50 px-2 py-1 text-xs font-medium text-red-800 ring-1 ring-inset ring-red-600/20">
            Ditolak
          </span>
        )
      default:
        return null
    }
  }

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    })
  }

  const formatCreatedAt = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      day: 'numeric',
      month: 'short',
      year: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    })
  }

  return (
    <div className="bg-card border border-border rounded-lg p-6 shadow-sm hover:shadow-md transition-shadow">
      <div className="flex items-start justify-between mb-4">
        <div>
          <h3 className="text-lg font-semibold text-foreground">{request.name}</h3>
          <p className="text-sm text-muted-foreground">Diajukan pada {formatCreatedAt(request.createdAt)}</p>
        </div>
        {getStatusBadge(request.status)}
      </div>

      <div className="space-y-3 mb-6">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <p className="text-sm font-medium text-muted-foreground">Barang/Kendaraan</p>
            <p className="text-sm text-foreground font-medium">{request.item}</p>
          </div>
          <div>
            <p className="text-sm font-medium text-muted-foreground">Tanggal Peminjaman</p>
            <p className="text-sm text-foreground font-medium">{formatDate(request.date)}</p>
          </div>
        </div>
        
        <div>
          <p className="text-sm font-medium text-muted-foreground mb-1">Keperluan Dinas</p>
          <p className="text-sm text-foreground bg-muted/50 p-3 rounded-md">{request.purpose}</p>
        </div>
      </div>

      {request.status === "pending" && (
        <div className="flex gap-3 pt-4 border-t border-border">
          <button 
            onClick={handleApprove}
            disabled={isProcessing}
            className="flex-1 inline-flex items-center justify-center rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-green-600 text-white hover:bg-green-700 h-9 px-4 py-2"
          >
            {isProcessing ? "Memproses..." : "Setujui"}
          </button>
          <button 
            onClick={handleReject}
            disabled={isProcessing}
            className="flex-1 inline-flex items-center justify-center rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-red-600 text-white hover:bg-red-700 h-9 px-4 py-2"
          >
            {isProcessing ? "Memproses..." : "Tolak"}
          </button>
        </div>
      )}
    </div>
  )
}
