"use client"

import { useEffect, useState } from "react"
import { BorrowRequest, fetchRequests, updateRequestStatus } from "../lib/api"
import RequestCard from "./RequestCard"

export default function RequestList() {
  const [requests, setRequests] = useState<BorrowRequest[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [filter, setFilter] = useState<'all' | 'pending' | 'approved' | 'rejected'>('all')

  const loadRequests = async () => {
    try {
      setLoading(true)
      setError(null)
      const data = await fetchRequests()
      setRequests(data)
    } catch (err: any) {
      setError(err.message || "Gagal memuat permintaan")
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    loadRequests()
  }, [])

  const handleApprove = async (id: string) => {
    try {
      await updateRequestStatus(id, "approved")
      await loadRequests() // Refresh the list
    } catch (err: any) {
      setError(err.message || "Gagal menyetujui permintaan")
    }
  }

  const handleReject = async (id: string) => {
    try {
      await updateRequestStatus(id, "rejected")
      await loadRequests() // Refresh the list
    } catch (err: any) {
      setError(err.message || "Gagal menolak permintaan")
    }
  }

  const filteredRequests = requests.filter(request => {
    if (filter === 'all') return true
    return request.status === filter
  })

  const getRequestCounts = () => {
    return {
      all: requests.length,
      pending: requests.filter(r => r.status === 'pending').length,
      approved: requests.filter(r => r.status === 'approved').length,
      rejected: requests.filter(r => r.status === 'rejected').length,
    }
  }

  const counts = getRequestCounts()

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-center space-y-4">
          <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto"></div>
          <p className="text-muted-foreground">Memuat permintaan...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {error && (
        <div className="bg-red-50 border border-red-200 text-red-800 px-4 py-3 rounded-md">
          <p className="text-sm font-medium">{error}</p>
          <button 
            onClick={loadRequests}
            className="mt-2 text-sm underline hover:no-underline"
          >
            Coba lagi
          </button>
        </div>
      )}

      {/* Filter Tabs */}
      <div className="border-b border-border">
        <nav className="flex space-x-8">
          {[
            { key: 'all', label: 'Semua', count: counts.all },
            { key: 'pending', label: 'Menunggu', count: counts.pending },
            { key: 'approved', label: 'Disetujui', count: counts.approved },
            { key: 'rejected', label: 'Ditolak', count: counts.rejected },
          ].map((tab) => (
            <button
              key={tab.key}
              onClick={() => setFilter(tab.key as any)}
              className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors ${
                filter === tab.key
                  ? 'border-primary text-primary'
                  : 'border-transparent text-muted-foreground hover:text-foreground hover:border-muted-foreground'
              }`}
            >
              {tab.label} ({tab.count})
            </button>
          ))}
        </nav>
      </div>

      {/* Request List */}
      {filteredRequests.length === 0 ? (
        <div className="text-center py-12">
          <div className="w-16 h-16 bg-muted rounded-full flex items-center justify-center mx-auto mb-4">
            <div className="w-8 h-8 bg-muted-foreground/20 rounded-full"></div>
          </div>
          <h3 className="text-lg font-medium text-foreground mb-2">
            {filter === 'all' ? 'Belum ada permintaan' : `Tidak ada permintaan ${
              filter === 'pending' ? 'yang menunggu' :
              filter === 'approved' ? 'yang disetujui' : 'yang ditolak'
            }`}
          </h3>
          <p className="text-muted-foreground">
            {filter === 'all' 
              ? 'Permintaan peminjaman akan muncul di sini setelah diajukan oleh peminjam.'
              : 'Coba ubah filter untuk melihat permintaan lainnya.'
            }
          </p>
        </div>
      ) : (
        <div className="grid gap-6">
          {filteredRequests.map(request => (
            <RequestCard 
              key={request.id} 
              request={request}
              onApprove={handleApprove}
              onReject={handleReject}
            />
          ))}
        </div>
      )}
    </div>
  )
}
